html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
  background-color: #f6f8fa; }
  body.not-uploaded {
    height: 100vh;
    display: grid;
    grid-template-rows: 6.4rem auto;
    row-gap: 1.6rem; }

p {
  text-align: justify; }

a {
  text-decoration: none; }

button {
  border: none;
  outline: none;
  cursor: pointer; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.header {
  height: 6.4rem;
  display: flex;
  align-items: center;
  background-color: #682444; }

.nav {
  padding: 0 2.4rem; }
  .nav .logo {
    display: flex;
    color: #FFF;
    font-size: 3rem; }

main.not-uploaded {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%; }

.upload-section {
  background-color: #FFF;
  border-radius: 1rem;
  padding: 3.2rem;
  border: 1px solid #e1e4e8;
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.1s; }
  .upload-section.highlight {
    border: 3px dashed #36A3B5; }
  @media (min-width: 600px) {
    .upload-section {
      width: 50%; } }
  .upload-section h2 {
    margin-top: 0;
    margin-bottom: 2.4rem;
    text-align: center; }

.form .field p {
  margin: 0;
  margin-bottom: 3.2rem;
  line-height: 1.5; }

.form input[type="submit"] {
  display: none; }

.select-input {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #fafbfc;
  color: #36A3B5;
  width: 100%;
  margin: 0 auto;
  height: 3.2rem;
  padding-left: 1.6rem;
  cursor: pointer; }
  @media (min-width: 600px) {
    .select-input {
      width: 15rem; } }
  .select-input::after {
    content: "";
    position: absolute;
    top: 1.33333rem;
    right: 1.6rem;
    border: 0.53333rem solid;
    border-color: #676f77 transparent transparent transparent;
    cursor: pointer;
    transition: 0.5s ease-in-out; }
  .select-input:hover, .select-input.selected {
    background-color: #f3f4f6; }

.month-container {
  display: grid;
  grid-template-columns: repeat(4, 4.8rem);
  grid-template-rows: repeat(3, 4.8rem);
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #f3f4f6;
  width: fit-content;
  margin: 0 auto 0 auto;
  height: 0;
  overflow: hidden;
  opacity: 0.01;
  transition: 0.5s; }
  .month-container input {
    width: 1px;
    height: 1px;
    display: inline-block;
    position: absolute;
    opacity: 0.01; }
  .month-container label {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
    .month-container label:nth-of-type(1) {
      border-top-left-radius: 1rem; }
    .month-container label:nth-of-type(4) {
      border-top-right-radius: 1rem; }
    .month-container label:nth-of-type(9) {
      border-bottom-left-radius: 1rem; }
    .month-container label:nth-of-type(12) {
      border-bottom-right-radius: 1rem; }
    .month-container label:hover {
      background-color: #36A3B5;
      color: #FFF; }
  .month-container input:checked + label {
    background-color: #36A3B5;
    color: #FFF; }

.select-input.selected + .month-container {
  margin-top: 0.8rem;
  opacity: 1;
  height: 14.4rem; }

.select-input.selected::after {
  transform: rotate(180deg);
  top: 0.8rem; }

.upload-input-container {
  position: relative; }
  .upload-input-container input {
    width: 0px;
    height: 0px;
    position: absolute;
    opacity: 0.01s; }
  .upload-input-container .flex-upload-container {
    display: flex;
    justify-content: center; }
  .upload-input-container label {
    padding: 0.7rem 1.6rem;
    background-color: #682444;
    color: #FFF;
    font-weight: 100;
    border-radius: 1rem;
    cursor: pointer; }
    .upload-input-container label:hover {
      background-color: #42172b; }

.loading-container {
  background-color: rgba(158, 169, 179, 0.3);
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  border-radius: 1rem;
  border: 1px solid #e1e4e8;
  background-color: #FFF; }

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #682444;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #682444 transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes popUpIn {
  0% {
    opacity: 0;
    margin-top: -90%; }
  75% {
    margin-top: 10%; }
  100% {
    opacity: 1; } }

@keyframes popUpOut {
  0% {
    opacity: 1; }
  75% {
    opacity: 1;
    margin-top: -50%; }
  100% {
    opacity: 0;
    margin-top: 30%; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    background-color: rgba(85, 85, 85, 0.8); }
  100% {
    background-color: rgba(85, 85, 85, 0); } }

@keyframes popInCross {
  0% {
    opacity: 0;
    width: 0;
    height: 0; }
  50% {
    opacity: 0.2;
    width: 0;
    height: 0; }
  100% {
    opacity: 1;
    width: 0.5rem;
    height: 5rem; } }

body.alert {
  overflow: hidden; }

.alert-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(85, 85, 85, 0.8);
  display: flex;
  justify-content: center;
  align-items: center; }
  .alert-container.in {
    animation: fadeIn 0.3s; }
  .alert-container.out {
    animation: fadeOut 1s; }
  .alert-container .alert {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #FFF;
    border-radius: 1rem;
    padding: 2.4rem 1.6rem 1.6rem 1.6rem; }
    .alert-container .alert.in {
      animation: popUpIn 0.3s; }
    .alert-container .alert.out {
      animation: popUpOut 0.5s;
      opacity: 0; }
    .alert-container .alert span {
      position: relative;
      width: 9rem;
      height: 9rem;
      border-radius: 50%;
      border: 5px solid #9C3927;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 3.2rem; }
      .alert-container .alert span::before {
        content: "";
        width: 0.5rem;
        height: 5rem;
        background-color: #9C3927;
        transform: rotate(45deg);
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        border-radius: 1rem;
        animation: popInCross 0.5s; }
      .alert-container .alert span::after {
        content: "";
        width: 0.5rem;
        height: 5rem;
        background-color: #9C3927;
        transform: rotate(-45deg);
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        border-radius: 1rem;
        animation: popInCross 0.5s; }
    .alert-container .alert .alert-title {
      font-size: 3.2rem;
      font-weight: 700;
      color: #3a434b;
      margin-bottom: 1.6rem; }
    .alert-container .alert .alert-info {
      color: #3a434b;
      font-size: 1.76rem;
      margin-bottom: 3.2rem; }
    .alert-container .alert .button {
      background-color: #36A3B5;
      width: 20%;
      height: 5rem;
      text-transform: uppercase;
      color: #FFF;
      border-radius: 1rem; }
      .alert-container .alert .button:hover {
        background-color: #2a808e; }
    .alert-container .alert p {
      margin: 0; }

.table-container {
  padding: 4.8rem; }

.person-table {
  width: 100%;
  border-spacing: 0;
  border: 1px solid #e1e4e8;
  border-radius: 1rem; }
  .person-table .row-title {
    background-color: #682444;
    color: #FFF; }
    .person-table .row-title th {
      font-weight: 400;
      font-size: 2rem;
      padding: 0.9rem 0; }
      .person-table .row-title th:first-of-type {
        border-top-left-radius: 1rem; }
      .person-table .row-title th:last-of-type {
        border-top-right-radius: 1rem; }
  .person-table .data-row:nth-of-type(2n+1) {
    background-color: #d5dfe8; }
  .person-table .data-row:nth-of-type(2n) {
    background-color: #FFF; }
  .person-table .data-row:last-of-type td:first-of-type {
    border-bottom-left-radius: 1rem; }
  .person-table .data-row:last-of-type td:last-of-type {
    border-bottom-right-radius: 1rem; }
  .person-table .data-row td {
    padding: 0.7rem 0;
    text-align: center; }
